@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500&display=swap);
html, body {
  max-width: 100%;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
  helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  transition: 0.5s all;
}


.home,
.events,
.products,
.faqs {
  padding: 20px 100px 20px 100px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

h6 {
  font-size: 40px;
  margin-bottom: 10px;
}

h8 {
  font-size: 20px;
}

.navbar {
  color: black;
  /* border-bottom: 3px solid #026857; */
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  top: 0;
  transition: 20ms;
  z-index: 5;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  
}

.navbar.active {
  background: black;
  color: white;
}

table, th, td {
  border: 1px solid black;
  border-collapse: collapse;
  
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

@media (max-width: 600px) {
  body {
    background-color: white;
  }
  
  .events {
    padding: 15px;
  }

}
  @font-face {
    font-family: 'retroica';
    src: url(/static/media/Retroica.56158ba4.ttf);
    font-style: normal;
    font-weight: 100;
  }

  .logo {
    font-family: retroica;
    cursor: pointer;
    font-size: 37px;
    margin-left: 20px;
  }

  .logo-title {
    cursor: pointer;
    display: inline-block;
    position: relative;
    font-size: 25px;
    margin-left: 25px;
    font-weight: 200;
  }

  .logo-title-moved {
    cursor: pointer;
    transition-delay: 37ms;
    font-weight: 200;
    transform: translate(200px);  
  }

  .icon-logo {
    display: none;

  }

  .dropdown {
    position: relative;
    display: inline-block;
  }

  .dropbtn {
    display: flex;
    align-items: center;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background: white;
    border: 2px solid black;
    min-width: 230px;
    z-index: 1;
    top: 100%;
    margin-top: -5px;
  }

  .dropdown-content a {
    color: black;
    font-size: 20px;
    font-weight: 400;
  }

  .dropdown-content a:hover {
    text-decoration: underline;
  }

  .dropdown:hover .dropdown-content, .dropdown-content:hover {
    display: block;
  }
  
  @media (max-width: 600px) {
    .logo-title {
      display: none;
    }
    
    .logo-title-moved {
        display: none;
    }

    .icon-logo {
        display: inline-block;
        position: relative;
        display: initial;
        font-size: 25px;
        margin-left: 20px;
        font-weight: 200;
    }
      
  }
  
  
@font-face {
    font-family: 'retroica';
    src: url(/static/media/Retroica.56158ba4.ttf);
    font-style: normal;
    font-weight: 100;
}

body {
    color: black;
}

.bxsci {
    font-size: 100px;
    font-weight: 400;
    font-family: retroica;
}

.scioly {
    font-size: 100px;
    margin-bottom: 20px;
    font-weight: 500;
    font-family: retroica;
}

.desc {
    font-size: 40px;
    font-weight: 200;
    font-family: retroica;
}

.scioly:hover, .bxsci:hover, .desc:hover {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.rotated {
    transform: rotate(-180deg);
}

.show-answer {
    transform: translateY(-40px);
}

.blob-1 {
    height: 45%;
    position: absolute;
    left: -14%;
}

.blob-4 {
    height: 500px;
    position: absolute;
    left: -14%;
    top: 1000px;
}

.blob-2 {
    height: 500px;
    position: absolute;
    right: -13%;
    bottom: 10%;
}

.blob-3 {
    height: 500px;
    position: absolute;
    right: -12%;
    top: 1200px;
    transform: rotate(60deg);
}

.page {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-text {
    font-weight: 400;
    font-size: 20px;
}

.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 450px;
  }

.about-title {
    font-size: 35px;
    margin-bottom: 50px;
}

.item-icon {
   font-size: 55px; 
}

.number {
    font-size: 50px;
    font-weight: 600;
}

.descript {
    font-size: 18px;
    margin-top: 70px;
    text-align: center;
}


@media (max-width: 600px) {
    .bxsci{
        font-size: 35px;
    }
    .scioly{
        font-size: 35px;
    }
    .desc{
        font-size: 25px;
    }
    .rotated {
        font-size: 20px;
    }
    .show-answer {
        font-size: 20px;
    }

    .blob-1, .blob-2, .blob-3, .blob-4 {
        display: none;
    }

    .about-text {
        font-size: 16px;
        text-align: left;
    }

}
